.feedPage{
  padding-top: 5em;
  text-align: center;
  margin: auto;
}

.material-img{
  height: 250px;
  padding-top: 5em;
  text-align: center;
}

.banner-text{
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
  text-align: center;
}

.banner-text h1 {
  font-size: 60px;
  font-weight: bold;
  color: white;
  margin: auto;
  text-align: center;
}

.banner-text h2 {
  font-size: 27px;
  font-weight: bold;
  color: white;
  margin: auto;
  text-align: center;
}

.banner-text hr{
  border-top: 2px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-size: 13px;
  padding: 1em;
}

.social-links{
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
}

.social-links i {
  color: white;
  font-size: 5em;
  padding: 8px;
}

.foot{
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 2.5rem;
}